import { Component } from 'react'
import { connect } from 'react-redux';
import { addAction } from 'reducer/actionCreators';

class Form extends Component {
  state = {
    text: '',
    price: ''
  };

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  clear = () => {
    this.setState({
      text: '',
      price: ''
    });
  };

  handleSubmit = (e) => {
    e.preventDefault();
    if (this.state.price === '0') {
      return alert('Cannot set 0 to amount!');
    }
    if (this.state.text && this.state.price) {
      this.props.add(this.state);
      this.clear(); 
    } else {
      return alert('Empty values!');
    }
  };

  render() {
    return (
      <form className="Add__form" onSubmit={this.handleSubmit}>
        Text: <br/>
        <input
          name="text"
          placeholder="Enter text"
          className="form__text"
          value={this.state.text}
          onChange={this.handleChange}
        /><br /><br/>
        Price: <br/>
        <input
          type="number"
          name="price"
          placeholder="Enter amount"
          className="form__price"
          value={this.state.price}
          onChange={this.handleChange}
        /><br /><br/>
        <input className="form__submit" type="submit" value="Add transaction"/>
      </form>
    );
  }
}

const mapStateToProps = (state) => ({
  
});

const mapDispatchToProps = (dispatch) => {
  return {
    add: (payload) => dispatch(addAction(payload))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Form);
