import React, { Component } from "react";
import PropTypes from "prop-types";
import { removeAction } from "reducer/actionCreators";
import { connect } from "react-redux";

export class Item extends Component {
  remove = (index) => {
    this.props.remove({ index });
  };

  render() {
    return (
      <li className="items__item">
        {this.props.children}
        <span className="item__price">
          <span style={{ verticalAlign: "middle" }}>
            {this.props.type === "income" ? "+" : ""}
          </span>
          {this.props.price}
        </span>
        <div
          className={`${
            this.props.type === "income" ? "item__line-green" : "item__line-red"
          }`}
        />
        <div
          className="item__remove"
          onClick={() => this.remove(this.props.index)}
        >
          X
        </div>
      </li>
    );
  }
}

Item.propTypes = {
  type: PropTypes.string.isRequired,
  price: PropTypes.number.isRequired,
  index: PropTypes.number.isRequired,
};

const mapDispatchToProps = (dispatch) => {
  return {
    remove: (payload) => dispatch(removeAction(payload)) 
  };
};

export default connect(null, mapDispatchToProps)(Item);
