import { Component } from 'react'
import { connect } from 'react-redux';

class IncomeExpense extends Component {
  render() {
    return (
      <div className="IncomeExpense">
        <div className="IncomeExpense__left">
          <div>
            Income <br/>
            <span className="IncomeExpense__income">
              ${this.props.income}
            </span>
          </div>
        </div>
        <div className="IncomeExpense__right">
          <div>
            Expense <br/>
            <span className="IncomeExpense__expense">
              ${this.props.expense}
            </span>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  income: state.income,
  expense: state.expense
});

const mapDispatchToProps = {
  
};

export default connect(mapStateToProps, mapDispatchToProps)(IncomeExpense);
