import { ADD, REMOVE } from "./actionTypes";
import initialState from "./initialState";

function reducer(state = initialState, { payload, type }) {
  let income;
  let expense;
  let type_;
  switch (type) {
    case ADD:
      const { text, price } = payload;
      type_ = Number(price) > 0 ? "income" : "expense";
      expense = type_ === "income" ? state.expense : Number(state.expense) + Number(Math.abs(price));
      income = type_ === "income" ? Number(state.income) + Number(price) : state.income;
      console.log(type_);

      return {
        ...state,
        expense,
        income,
        balance: income - expense,
        history: [
          ...state.history,
          {
            text,
            price,
            type: type_,
          },
        ],
      };
    case REMOVE:
      const { index } = payload;
      const newHistory = [...state.history];
      income = state.income;
      expense = state.expense;
      if (newHistory[index].type === 'income') {
        income = Number(state.income) - Number(newHistory[index].price);
      } else {
        expense = Number(state.expense) + Number(newHistory[index].price);
      }
      newHistory.splice(index, 1);

      return {
        ...state,
        history: newHistory,
        income,
        expense,
        balance: income - expense
      };
  }
  return state;
}

export default reducer;
